import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes';

class ComponentAdvancedLight extends Component {
    constructor() {
        super();

        this.componentName = "advanced light";
        this.createPort("Color", ComponentPortTypes.In, ComponentValueTypes.Color);
        this.createPort("Brightness", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createAction("Toggle Light", this.toggleLight);
    }

    toggleLight(component, action) {
        
    }
}

export default ComponentAdvancedLight;


