import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes.js';

class ComponentHSV2Hex extends Component {
    constructor() {
        super();

        this.componentName = "hsv to hexadecimal";
        this.createPort("Hue", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("Saturation", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("Value", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("Hexadecimal RGB", ComponentPortTypes.Out, ComponentValueTypes.Color);
        this.createAction("Convert", this.convert);
        this.createAction("On Converted", this.onConverted);
    }

    convert(component, action) {
        if (component.getPort("Hue").value != null && component.getPort("Saturation").value != null && component.getPort("Value").value != null) {
            let h = component.getPort("Hue").value;
            let s = component.getPort("Saturation").value / 255;
            let v = component.getPort("Value").value / 255;
            let c = s * v;
            let x = c * (1 - Math.abs((h / 60) % 2 - 1));
            let m = v - c;
            let rgb = [0, 0, 0];

            if (h < 60) {
                rgb[0] = c;
                rgb[1] = x;
            } else if (h < 120) {
                rgb[0] = x;
                rgb[1] = c;
            } else if (h < 180) {
                rgb[1] = c;
                rgb[2] = x;
            } else if (h < 240) {
                rgb[1] = x;
                rgb[2] = c;
            } else if (h < 300) {
                rgb[0] = x;
                rgb[2] = c;
            } else if (h < 360) {
                rgb[0] = c;
                rgb[2] = x;
            }
            rgb[0] = Math.floor((rgb[0] + m) * 255);
            rgb[1] = Math.floor((rgb[1] + m) * 255);
            rgb[2] = Math.floor((rgb[2] + m) * 255);

            let hex1 = rgb[0].toString(16);
            let hex2 = rgb[1].toString(16);
            let hex3 = rgb[2].toString(16);

            if (hex1.length === 1) hex1 = "0" + hex1;
            if (hex2.length === 1) hex2 = "0" + hex2;
            if (hex3.length === 1) hex3 = "0" + hex3;

            component.getPort("Hexadecimal RGB").setValue("#" + hex1 + hex2 + hex3);
        } else {
            component.getPort("Hexadecimal RGB").setValue("#000000");
        }
        
        component.runAction("On Converted");
    }

    onConverted(component, action) {
        
    }
}

export default ComponentHSV2Hex;


